import React, { useState } from "react";
import { motion } from "framer-motion";
import { Link } from "react-router-dom";
import "../styles/finalCta.css";
import SweetPopup from "./sweetpopup.js"; //  Import SweetPopup

const FinalCta = () => {
  const [isPopupOpen, setPopupOpen] = useState(false); //  Manage popup state

  return (
    <motion.div
      className="final-cta-container"
      initial={{ opacity: 0, y: 50 }}
      animate={{ opacity: 1, y: 0 }}
      transition={{ duration: 0.8 }}
    >
      <h2 className="cta-title">
        Your Car Story Starts Here. Lets Make It a Smooth One. 
      </h2>
      <p className="cta-description">
        Clean cars. Full checks. No regrets. We dont just sell  we deliver
        peace of mind.
      </p>
      <ul className="cta-benefits">
        <li> Foreign and Nigerian-used cars you can trust.</li>
        <li> Full papers. Sharp engines. No stories.</li>
        <li> Buy, sell, or swap  we make it simple and secure.</li>
      </ul>
      <p className="cta-action">Lets get you on the road with confidence.</p>

      <div className="cta-buttons">
        {/*  SweetPopup button */}
        <button
          className="cta-button primary"
          onClick={() => setPopupOpen(true)}
        >
           Start Your Purchase Now
        </button>

        {/*  Direct WhatsApp link (clean, no fbq tracking) */}
        <a
          href="https://wa.me/2347086805711"
          target="_blank"
          rel="noopener noreferrer"
          className="cta-button secondary"
        >
           Chat on WhatsApp
        </a>
      </div>

      {/*  SweetPopup Component */}
      <SweetPopup isOpen={isPopupOpen} onClose={() => setPopupOpen(false)} />
    </motion.div>
  );
};

export default FinalCta;
